import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import Button from '../Button/Button';
import './Contact.css';
import { validateEmail, validateSubject } from '../../utils/validation';

const Contact = () => {
  const [subject, setSubject] = useState('');
  const [email, setEmail] = useState('');
  const [description, setDescription] = useState('');
  const [errors, setErrors] = useState({});

  const handelChangeInputSubject = (event) => {
    if (validateSubject(event.target.value)) {
      setSubject(event.target.value);
      setErrors({
        ...errors,
        subject: null,
      });
    } else if (event.target.value === '') {
      setErrors({
        ...errors,
        subject: null,
      });
    } else {
      setErrors({
        ...errors,
        subject: 'Subject must be more than 8 characters',
      });
    }
  };

  const handelChangeInputEmail = (event) => {
    if (validateEmail(event.target.value)) {
      setEmail(event.target.value);
      setErrors({
        ...errors,
        email: null,
      });
    } else if (event.target.value === '') {
      setErrors({
        ...errors,
        email: null,
      });
    } else {
      setErrors({
        ...errors,
        email: 'Email is not valid',
      });
    }
  };

  const handelChangeTextArea = (event) => {
    setDescription(event.target.value);
  };

  const handelSubmit = () => {
    console.log('subject:', subject);
    console.log('email:', email);
    console.log('description:', description);
  };

  return (
    <div className="Contact">
      <div className="formControl">
        <TextField onChange={handelChangeInputSubject} id="outlined-basic" label="Subject" variant="standard" />
        {errors.subject && <span>{errors.subject}</span>}
      </div>
      <div className="formControl">
        <TextField onChange={handelChangeInputEmail} id="standard-basic" label="Email" variant="standard" />
        {errors.email && <span>{errors.email}</span>}
      </div>
      <div className="formControl">
        <TextField
          onChange={handelChangeTextArea}
          id="outlined-multiline-static"
          label="Description"
          multiline
          rows={4}
          defaultValue="Your Request... . "
        />
      </div>
      <div className="formControl">
        <Button added="primary" handelClick={handelSubmit}>Submit</Button>
      </div>
    </div>
  );
};

export default Contact;
